import React from "react";
import { Row, Container, Col } from "react-bootstrap";
import RecentWorks from "@screens/main/main_v2/recent-works";

const PortfolioScreen = () => {
  return (
    <Container>
      <Row className="portfolio_recent_works">
        <Col md={12}>
          <RecentWorks />
        </Col>
      </Row>
    </Container>
  );
};

export default PortfolioScreen;
